.searchBarContent {
  width: 75%;
  border: 0;
  outline: 0;
  display: flex;
  align-items: stretch;
  border-radius: 3px;
  margin: auto;
}

.searchBarContent .searchBarInput {
  flex: 1;
  outline: 0;
  padding: 10px;
  font-size: 14px;
  border: 2px solid rgb(12 44 201);
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  border-right: 0;
}

.searchBarInput:focus {
  border-color: rgb(1, 25, 148);
  border-width: 3px;
}

.searchBarBtn {
  padding: 10px;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  cursor: pointer;
  font-size: 14px;
  border: 0;
  outline: 0;
  background: var(--primary-color);
  color: #f3f3f3;
  height: 100%;
}
.searchBarBtn:hover {
  background: rgb(8, 43, 221);
}

@media (max-width: 850px) {
  .searchBarContent {
    width: 95%;
    margin: auto;
  }
}
